const rootRoute = {
  path: '/profil/sportif/animation/incentive/specifique',

  getComponents(nextState, callback) {
    require.ensure([], function (require) {
      callback(null, require('../../../../../../components/animation_incentive_sportif/DemandeSpecifiqueAnimationIncentiveSportif').default)
    })
  }
};

export default rootRoute;


